@import url(https://fonts.googleapis.com/css?family=Lato:400,500,600,700,800,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: 'Lato', sans-serif; }
  body h1 {
    font-size: 18px; }
  body .close-icon {
    cursor: pointer; }
  body .text-title {
    font-weight: 800; }
  body .input-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-content: center;
    align-items: center; }
  body .green-link {
    font-weight: 600;
    text-decoration: underline;
    color: #44CD88;
    cursor: pointer; }
  body input {
    margin: 0 0.5rem;
    padding: 5px;
    height: 35px;
    border-radius: 3px;
    border: solid 1px #CBE2D7; }
    body input:focus {
      border: solid 1px #44CD88; }
  body select {
    margin: 0 0.5rem;
    padding: 5px;
    border-radius: 3px;
    border: solid 1px #5a5a5a; }
    body select:focus {
      border: solid 1px #44CD88; }
  body .hint-text {
    font-style: italic;
    color: #acacac;
    font-size: 14px;
    padding: 0.3rem 1rem; }
  body .green-button {
    border-style: none;
    background-color: #AFEA7D;
    color: #272E2E;
    font-weight: 800;
    border-radius: 2px;
    padding: 0.7rem 1rem;
    min-width: 120px;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    cursor: pointer; }
    body .green-button:hover {
      background-color: #5ad67f; }
  body .dark-button {
    border-style: none;
    background-color: #272E2E;
    color: white;
    font-weight: 800;
    border-radius: 2px;
    padding: 0.7rem 1rem;
    min-width: 120px;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    cursor: pointer; }
    body .dark-button:hover {
      background-color: #191d1d; }
  body .card-section {
    border-bottom: solid 1px #dbdbdb;
    padding: 1rem 0; }
  body #card-for-tagging {
    position: absolute;
    background-color: white; }

